import React, { Component } from 'react';
import InvoiceDetailes from './Components/InvoiceDetails';
import PaymentSelect from './Components/PaymentSelect';
import PaymentSuccess from './Components/PaymentSuccess';
import FawryPay from './Components/fawryPay';
import CreditPay from './Components/CreditPay';
import { Dictionary } from './dictionary/MainDictionary';
// import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter, Router, Redirect, Route, Link } from 'react-router-dom';
import osale_logo from './img/osale-logo.png';

import main_logo from './img/birds-logo.png';
export default class App extends Component {
  constructor() {
    super();
    localStorage.lang = localStorage.lang || 'ar';
    this.state = {
      paymentStatus: 0,
      redirectAvailable: '',
      language: localStorage.lang || 'ar',
    };
  }

  componentDidMount() {
    console.log(this.state.language);
    this.setLang();
  }

  setLang = (lang = localStorage.lang) => {
    let style = `html {
    direction:  ${lang == 'ar' ? 'rtl' : 'ltr'};
}

body#body {
    text-align: ${lang == 'ar' ? 'right' : 'left'};
    font-family: ${lang == 'ar' ? 'GE SS Two Medium' : 'lato'}
}

span.rtl {
    float: ${lang == 'ar' ? 'right' : 'none'};
    margin: 0.5rem;
} 
.button--language{
  font-family:${lang == 'ar' ? 'lato' : 'GE SS Two Medium'}
}
`;
    let element = document.getElementById('lang-control');

    element.append(style);
    console.log(element);
  };

  toogleLanguage = () => {
    if (this.state.language == 'ar') {
      localStorage.lang = 'en';
      this.setState({ language: 'en' });
    } else {
      localStorage.lang = 'ar';
      this.setState({ language: 'ar' });
    }
    this.setLang();
  };
  paymentRouting = (e) => {
    let fawryChecked = document.getElementById('fawry-payment').checked;
    let creditChecked = document.getElementById('credit-payment').checked;
    if (fawryChecked) {
      this.setState({ redirectAvailable: 'fawry' });
    } else if (creditChecked) {
      this.setState({ redirectAvailable: 'credit' });
    } else {
      document.getElementById('selection-null').style.display = 'block';
      setTimeout(() => {
        try {
          document.getElementById('selection-null').style.display = 'none';
        } catch (error) {}
      }, 3000);
    }
    e.preventDefault();
  };

  routeToHome = () => {
    this.setState({ redirectAvailable: '/' });
  };

  paymentSuccess = () => {
    this.setState({ redirectAvailable: '/' });
    this.setState({ paymentStatus: 1 });
  };

  render() {
    let { paymentStatus, redirectAvailable } = this.state;
    let lang = localStorage.lang;
    let redirectionElement = null;
    if (redirectAvailable) {
      redirectionElement = <Redirect to={redirectAvailable} />;
    }

    return (
      <BrowserRouter>
        <div className='container'>
          <div className='container'>
            <div className='row justify-content-between'>
              <div className='col-4 col-sm-3 col-lg-2'>
                <img src={main_logo} alt='company logo' className='w-100' />
                <p className='font-1' style={{ fontFamily: 'lato' }}>
                  Powered By Osale
                </p>
              </div>
              <div className='col-3 col-sm-2'>
                <button
                  onClick={this.toogleLanguage}
                  className='btn btn-light bg-transparent border-transparent button--language'>
                  {Dictionary.language[lang]}
                </button>
              </div>
            </div>
          </div>
          <InvoiceDetailes />
          {redirectionElement}
          <Route
            exact
            path='/'
            component={
              !paymentStatus
                ? () => <PaymentSelect paymentRouting={this.paymentRouting} />
                : PaymentSuccess
            }
          />
          <Route
            exact
            path='/fawry'
            component={() => (
              <FawryPay
                routeToHome={this.routeToHome}
                paymentSuccess={this.paymentSuccess}
              />
            )}
          />
          <Route
            exact
            path='/credit'
            component={() => (
              <CreditPay
                routeToHome={this.routeToHome}
                paymentSuccess={this.paymentSuccess}
              />
            )}
          />
          {/* <div className='row justify-content-center bg-black'>
            <div className='col-sm-1 col-2'>
              <img src={osale_logo} alt='osale logo' className='w-100' />
            </div>
          </div> */}
        </div>
      </BrowserRouter>
    );
  }
}
